import { useEffect, useState } from 'react';
import Monitor from './Monitor'

interface Stopover {
    flightNumber: string;
    departure: string;
    destination: string;
    departureDate: string;
    destinationDate: string;
    price: number;
    isSoldOut: boolean;
    link: string;
    airline: string;
}

interface MonitorItem {
    title: string;
    request_id: string;
    flightData: {
        stopover: Stopover[];
    };
    email: string;
}

function MonitorPage() {
    const [data, setData] = useState<MonitorItem[]>([]);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch(`${process.env.REACT_APP_WAS_URL}/monitoring/list`); 
                if (!response.ok) {
                    throw new Error(' ');
                }
                const jsonData = await response.json();
                setData(jsonData); //
            } catch (error) {
                console.error(" fetch ", error);
            }
        };

        fetchData(); //  
    }, []);
    return <Monitor initialData={data} />; //sampleData->data  WAS  
}

export default MonitorPage;
